.required{
    border-left: darkred solid 8px; 
}

.notRequired{
    border-left: darkgreen solid 8px; 
}

form input.form-control{
    font-size: 16px;
    padding: 8px 12px;
    height: auto;
    font-weight: 600;
}

form button[type="submit"]{
    padding: 12px 0 10px;
    font-size: 18px;
    font-weight: 600; 
}